        {
          label: 'Status',
          prop: 'status',
          __slotName: 'status',
          'min-width': '140',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
        },
      ],
      // 试算功能部分
      budgetDialogVisible: false,
      isNoCharge: true,
      budgetTableData: [],
      primaryOrganizationName: '',
      receiverIdentityName: '',
      budgetColumns: [
        { label: 'Charge Payer', prop: 'chargePayerIdentityName', align: 'left' },
        { label: 'Charge Amount', prop: 'chargeAmount', align: 'left', formatter: currencyFormatter },
        { label: 'Account Type', prop: 'accountTypeAlias', align: 'left' },
        { label: 'Account Balance', prop: 'availableAmount', align: 'left', formatter: currencyFormatter },
      ],
      noChargePropColumns: [
        {
          label: 'Charge Amount',
          value: 'No Charge',
          itemKey: 'chargeAmount',
        },
        {
          label: 'Initiator Identity',
          value: '-',
          itemKey: 'primaryOrganizationName',
        },
        {
          label: 'Receiver Identity',
          value: '-',
          itemKey: 'receiverIdentityName',
        },
        {
          label: 'Transaction Amount',
          value: '-',
          itemKey: 'amount',
        },
      ],
      bankList: [],
      selectedReasonTypeData: {},
      bankInfoLoading: false,
      bankInfos: [
        {
          label: 'Bank Short Code',
          value: 'bankShortCode',
          content: '',
        },
        {
          label: 'Bank Address',
          value: 'address',
          content: '',
        },
        {
          label: 'Finance Service',
          value: 'financeService',
          content: '',
        },
        {
          label: 'Trust Bank',
          value: 'trustBank',
          content: '',
        },
        {
          label: 'Transfer Mode',
          value: 'transferMode',
          content: '',
        },
      ],
      transferModeOptions: [],
      selectRequiredValidateObj,
      currentOrganizationIdentityId: '',
    };
  },
  computed: {
    identityId() {
      if (this.$route.query.data) {
        const queryData = decodeURIComponent(this.$route.query.data);
        return JSON.parse(queryData).identityId || '';
      } else {
        return this.currentOrganizationIdentityId || '';
      }
    },
    propShortCode() {
      if (this.basicForm.payerShortCode) {
        return this.basicForm.payerShortCode;
      } else {
        return cookie.get('initiatorShortCode') || '';
      }
    },
    payeeShortCodeProp() {
      return this.sameIdentity ? 'noMustCheckShortCode' : 'payeeShortCode';
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case 'Active':
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
          case 'Completed':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case 'Pending Authorized':
          case 'Authorized':
          case 'Pending':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case 'Declined':
          case 'Cancelled':
          case 'Expired':
            return { 'background-color': `${this.GlobalStyleVariables.statusDark}` };
          default:
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
        }
      };
    },
    showBankAccount() {
      if (this.basicForm.reasonType) {
        return (
          this.reasonTypeList
            .filter((ele) => {
              return this.basicForm.reasonType === ele.value;
            })[0]
            .transactionType.transactionTypeProperties.filter((item) => item.propertyCode == 'Financial_Transactions')
            .length > 0
        );
      }
      return false;
    },
  },
  watch: {
    showBankAccount(val) {
      if (val) {
        this.getBankList();
      }
    },
    'basicForm.bankAccountId': {
      handler(val) {
        if (val) {
          this.getBankInfo(val);
        }
      },
    },
  },
  created() {
    this.getInitData();
  },
  methods: {
    getInitData() {
      queryOrganizationDetail({ identityId: '' }).then((res) => {
        if (res.header && res.header.success) {
          let organizationInfo = res.organizationInfo;
          if (organizationInfo) {
            this.currentOrganizationIdentityId = organizationInfo.organizationId;
            this.isSearch = true;
            this.search();
            this.basicForm.payerShortCode = this.propShortCode;
            this.getTransferMode();
          }
        }
      });
    },
    async getTransferMode() {
      this.transferModeOptions = [];
      let res = await getTransferMode();
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.jsonDataStr);
        dataDictJson.map((item) => {
          let transferModeObj = {
            value: item.code,
            label: item.value,
          };
          this.transferModeOptions.push(transferModeObj);
        });
      }
    },
    searchData() {
      this.search();
    },
    reSearchData() {
      this.searchServiceData = '';
      this.search();
    },
    async search() {
      const paramsSer = {
        serviceName: this.searchServiceData,
        identityId: this.identityId,
        identityType: '5000',
      };
      const transactionServiceData = await queryAvailableService(paramsSer);
      if (transactionServiceData) {
        this.serviceList = transactionServiceData.serviceList;
        this.serviceNameList = this.serviceList.map((item) => {
          return { label: item.serviceName, value: item.alias, description: item.description };
        });
      }
    },
    handleLabel(val) {
      this.basicForm.payeeVoucherCode = '';
      this.basicForm.payeeShortCode = '';
      this.basicForm.payeeTillMsisdn = '';
      this.basicForm.payeeMsisdn = '';
      this.basicForm.validValue = '';
      this.basicForm.secretCode = '';
      this.validVerifySuccess = false;
      switch (val) {
        case '04':
          return (this.currentLabel = 'Short Code');
        case '17':
          return (this.currentLabel = 'Till MSISDN');
        case '01':
          return (this.currentLabel = 'MSISDN');
        case '03':
          return (this.currentLabel = 'Voucher Code');
        default:
      }
    },

    handleSelectedService(val) {
      this.$refs.basicFormRef.validateField('serviceCode');
      this.validVerifySuccess = false;
      this.basicForm.diffIdentifierType = '';
      this.basicForm.reasonType = '';
      this.basicForm.payeeShortCode = '';
      this.basicForm.payerAccountNo = '';
      this.basicForm.payeeAccountNo = '';
      this.basicForm.payeeVoucherCode = '';
      this.basicForm.payeeMsisdn = '';
      this.basicForm.payeeTillMsisdn = '';
      this.basicForm.amount = '';
      this.basicForm.reasonText = '';
      this.basicForm.reason = '';
      this.basicForm.remark = '';
      this.basicForm.serviceEvaluation = '';
      this.basicForm.requesterMsisdn = '';
      this.basicForm.requesterIdentityType = '';
      this.searchServiceData = '';
      this.search();
      this.basicForm.payeeShortCode = '';
      this.$data.identifierTypeList = this.$options.data().identifierTypeList;
      if (val) {
        let currentService = this.serviceList.filter((ele) => {
          return val === ele.alias;
        })[0];
        this.description = currentService.description;
        this.isAutomatic = currentService.reasonSelectionMode == 'AUTOMATIC';
        this.getReasonTypeList(val);
      } else {
        this.description = '';
        this.basicForm.reasonType = '';
      }
    },
    async getTableData() {
      const params = {
        identityId: this.identityId,
      };

      const res = await queryAccountList(params).finally(() => {
        this.isSearch = false;
      });
      if (res) {
        this.accountList = res.list;
        return res;
      }
    },
    // 获取reasonType列表
    getReasonTypeList(serviceCode) {
      let paramsData = {
        serviceCode,
        identityId: this.identityId,
        serviceName: serviceCode,
        identityType: '5000',
      };

      queryAvailableReasonType(paramsData).then((res) => {
        if (res.header && res.header.success) {
          if (res.reasonTypeList === [] || res.reasonTypeList.length === 0) {
            this.showNoReasonTypeTips = true;
          } else {
            this.showNoReasonTypeTips = false;
            this.reasonTypeList =
              res.reasonTypeList &&
              res.reasonTypeList.map((item) => {
                return {
                  label: item.reasonName,
                  value: item.alias,
                  sameIdentity: item.transactionType.allowSameIdentity ? item.transactionType.allowSameIdentity : false,
                  transactionRoleInfo: item.transactionRoleInfo,
                  flowTemplateMap: item.flowTemplateMap,
                  transactionType: item.transactionType,
                  reasonIndex: item.reasonIndex,
                };
              });
            this.handlerSelectedReasonType(this.reasonTypeList[0]);
            this.basicForm.reasonType = this.reasonTypeList[0].value;
          }
        }
      });
    },

    // 选择ReasonType
    selectedReasonType(val) {
      let currentReasonType = this.reasonTypeList.filter((ele) => {
        return val === ele.value;
      });
      this.handlerSelectedReasonType(currentReasonType[0]);
    },
    handlerSelectedReasonType(currentReasonType) {
      this.$data.identifierTypeList = this.$options.data().identifierTypeList;
      this.selectedReasonTypeData = currentReasonType || {};
      this.sameIdentity = this.selectedReasonTypeData.sameIdentity;
      const { sameIdentity, transactionRoleInfo, flowTemplateMap } = this.selectedReasonTypeData;
      const { creditParty, debitParty } = transactionRoleInfo;
      const { templateId } = flowTemplateMap.WEB;
      let primaryDefaultAccountIndex = this.getPrimaryDefaultAccountIndex(creditParty, debitParty);
      let { receiverDefaultAccountIndex, receiverIdentityType } = this.getReceiverDefaultAccountIndex(
        creditParty,
        debitParty
      );
      this.primaryDefaultAccountIndex = primaryDefaultAccountIndex;
      this.receiverDefaultAccountIndex = receiverDefaultAccountIndex;
      this.getPrimaryAccountOptions(primaryDefaultAccountIndex);
      if (sameIdentity) {
        this.basicForm.diffIdentifierType = '04';
        this.identifierTypeList = [{ value: '04', label: 'Short Code' }];
        this.basicForm.payeeShortCode = this.basicForm.payerShortCode;
        this.getReceiverAccountOptions();
        if (receiverIdentityType == 'SP') {
          this.basicForm.diffIdentifierType = '10';
          this.identifierTypeList = [{ value: '10', label: 'SP' }];
        }
      } else {
        this.handelIdentityType(receiverIdentityType, templateId);
      }
    },
    handelIdentityType(receiverIdentityType, templateId) {
      if (receiverIdentityType === 'ORGANIZATION') {
        this.identifierTypeList = [
          { value: '04', label: 'Short Code' },
          { value: '17', label: 'Till MSISDN' },
        ];
      } else if (receiverIdentityType === 'CUSTOMER') {
        this.identifierTypeList = [{ value: '01', label: 'MSISDN' }];
      } else if (templateId === 'VoucherRedeeming') {
        this.identifierTypeList = [{ value: '03', label: 'Voucher Code' }];
      }
    },
    getPrimaryDefaultAccountIndex(creditParty, debitParty) {
      let primaryDefaultAccountIndex;
      if (creditParty.serviceRoleType === 'PRIMARY_PARTY') {
        primaryDefaultAccountIndex = creditParty.accountTypeIndex;
      } else if (debitParty.serviceRoleType === 'PRIMARY_PARTY') {
        primaryDefaultAccountIndex = debitParty.accountTypeIndex;
      }
      return primaryDefaultAccountIndex;
    },
    getReceiverDefaultAccountIndex(creditParty, debitParty) {
      let receiverDefaultAccountIndex, receiverIdentityType;
      if (creditParty.serviceRoleType === 'RECEIVER') {
        receiverDefaultAccountIndex = creditParty.accountTypeIndex;
        receiverIdentityType = creditParty.identityType;
      } else if (debitParty.serviceRoleType === 'RECEIVER') {
        receiverDefaultAccountIndex = debitParty.accountTypeIndex;
        receiverIdentityType = debitParty.identityType;
      }
      return { receiverDefaultAccountIndex, receiverIdentityType };
    },
    getPrimaryAccountOptions(primaryDefaultAccountIndex) {
      let currentTemp = [
        {
          label: 'Automatically selected by system',
          value: '',
        },
      ];
      this.accountList.forEach((item) => {
        if (item.accountTypeId == primaryDefaultAccountIndex) {
          this.payerAccountTypeId = item.accountTypeId;
          currentTemp.push({
            label: `Default Account (${item.accountName})`,
            value: item.accountNo,
            accountTypeId: item.accountTypeId,
          });
        }
      });
      this.primaryAccountList = currentTemp;
    },
    getReceiverAccountOptions(inputType, fieldValue) {
      // 判断同一组织下收款方的账户处理
      if (this.sameIdentity) {
        this.handleReceiverAccountOptions(this.accountList);
      } else {
        // 不同组织、客户之间收款方账户处理
        switch (inputType) {
          case 'shortCode':
            this.getAccountByShortCodeOrTillMsisdn('shortCode', fieldValue);
            break;
          case 'tillMsisdn':
            this.getAccountByShortCodeOrTillMsisdn('tillMsisdn', fieldValue);
            break;
          case 'msisdn':
            this.getAccountByMsisdn(fieldValue);
            break;
          case 'voucherCode':
            this.getAccountByVoucherCode(fieldValue);
            break;
          default:
        }
      }
    },
    async getAccountByShortCodeOrTillMsisdn(searchType, fieldValue) {
      let identityId = '';
      let params = searchType == 'shortCode' ? { shortCode: fieldValue } : { tillMsisdn: fieldValue };
      let orgRes = await verifyTransactionIdentity(params);
      if (orgRes.header && orgRes.header.success) {
        identityId = orgRes.identityId;
      }
      let accountRes = await queryAccountList({ identityId });
      if (accountRes.header && accountRes.header.success) {
        this.handleReceiverAccountOptions(accountRes.list);
      }
    },
    async getAccountByMsisdn() {
      let identityId = '';
      let customerIdentity = await verifyTransactionIdentity({ msisdn: this.basicForm.payeeMsisdn }); // customer接口获取identityId
      if (customerIdentity.header && customerIdentity.header.success) {
        identityId = customerIdentity.identityId;
      }
      let customAccountRes = await getCustomerAccount({ identityId }); // customer查询账户列表
      if (customAccountRes.header && customAccountRes.header.success) {
        customAccountRes.accounts.forEach((item) => {
          this.customAccountName = item.accountName;
        });
        this.handleReceiverAccountOptions(customAccountRes.accounts);
      }
    },
    getAccountByVoucherCode() {
      let currentTemp = [
        {
          label: 'Automatically selected by system',
          value: '',
        },
      ];
      this.receiverAccountList = currentTemp;
    },
    handleReceiverAccountOptions(accounts = []) {
      let currentTemp = [
        {
          label: 'Automatically selected by system',
          value: '',
        },
      ];
      accounts.forEach((item) => {
        if (item.accountTypeId == this.receiverDefaultAccountIndex) {
          this.payeeAccountTypeId = item.accountTypeId;
          currentTemp.push({
            label: `Default Account (${item.accountName})`,
            value: item.accountNo,
            accountTypeId: item.accountTypeId,
          });
        }
      });
      this.receiverAccountList = currentTemp;
    },
    verifyFun(verifyField, identifierType) {
      this.$refs['basicFormRef'].validateField(verifyField, (valid) => {
        if (!valid) {
          this.getReceiverAccountOptions(identifierType, this.basicForm[verifyField]);
          this.validVerifySuccess = true;
        } else {
          this.validVerifySuccess = false;
          return false;
        }
      });
    },
    reset() {
      this.$confirm('Are you sure to reset all data you have entered/selected in this page', {
        cancelButtonText: 'Cancel',
        confirmButtonText: 'Confirm',
        type: 'warning',
      })
        .then(() => {
          this.$refs.basicFormRef.resetFields();
        })
        .catch(() => {});
    },
    transactionParams() {
      const amountParam = this.basicForm.amount;
      const paramsData = {
        payeeAccountTypeId: this.payeeAccountTypeId == '' ? null : this.payeeAccountTypeId,
        payerAccountTypeId: this.payerAccountTypeId == '' ? null : this.payerAccountTypeId,
        identifierType: this.basicForm.diffIdentifierType,
        reasonType: this.basicForm.reasonType == '' ? null : this.basicForm.reasonType,
        serviceCode: this.basicForm.serviceCode,
        payerAccountNo: this.basicForm.payerAccountNo == '' ? null : this.basicForm.payerAccountNo, // 主交易方账户
        payeeVoucherCode: this.basicForm.payeeVoucherCode,
        validValue: this.basicForm.validValue,
        secretCode: this.basicForm.secretCode,
        payeeAccountNo: this.basicForm.payeeAccountNo == '' ? null : this.basicForm.payeeAccountNo, // 收款方账户
        amount: this.delcommafy(amountParam),
        remark: this.basicForm.remark,
        reasonMode: this.basicForm.reasonMode,
        reason: this.basicForm.reason,
        serviceEvaluation: this.basicForm.serviceEvaluation, // 评论
        requesterMsisdn: this.basicForm.requesterMsisdn,
        primaryParty: {
          identifierType: '04',
          identifier: this.propShortCode,
        },
        receiverParty: {
          identifierType: this.getDiffIdentifierType(),
          identifier: this.getReveiverIdentifier(),
        },
      };
      return paramsData;
    },
    async initTransactionPrepare() {
      try {
        this.submitLoading = true;
        const servicePreValidationResult = await queryServicePreValidation({
          serviceCode: this.basicForm.serviceCode,
        });
        if (servicePreValidationResult && servicePreValidationResult.header.success) {
          if (servicePreValidationResult.servicePreValidation) {
            const transactionParams = this.transactionParams();
            const budgetRes = await queryBudget(transactionParams);
            if (budgetRes && budgetRes.header.success) {
              if (JSON.stringify(budgetRes.payee) === '{}' && JSON.stringify(budgetRes.payer) === '{}') {
                this.isNoCharge = true;
                let tempProp = {
                  chargeAmount: 'No Charge',
                  primaryOrganizationName: budgetRes.primaryIdentityName,
                  receiverIdentityName: budgetRes.receiverIdentityName,
                  amount: AmountUnit.format(transactionParams.amount, budgetRes.currency),
                };
                this.noChargePropColumns.forEach((item) => {
                  item.value = tempProp[item.itemKey];
                });
              } else {
                this.isNoCharge = false;
                this.primaryOrganizationName = budgetRes.primaryIdentityName;
                this.receiverIdentityName = budgetRes.receiverIdentityName;
                this.budgetTableData = [budgetRes.payer, budgetRes.payee].filter(
                  (item) => Object.keys(item).length !== 0
                );
              }
              this.budgetDialogVisible = true;
            }
          } else {
            this.initiateTransactionSubmit();
          }
        }
      } catch (err) {
      } finally {
        this.submitLoading = false;
      }
    },
    initiateTransactionSubmit() {
      if (!this.submitInit) {
        return;
      }
      this.submitLoading = true;
      this.submitInit = false;
      const paramsData = this.transactionParams();
      initiateTransaction(paramsData)
        .then((res) => {
          this.submitLoading = false;
          if (res.header && res.header.success) {
            this.$refs.basicFormRef.resetFields();
            this.basicForm.serviceCode = '';
            this.validVerifySuccess = false;
            this.showInitiateOrSucceed = false;
            this.tradeOrderId = res.tradeOrderId;
            this.payerAccountTypeId = '';
            this.payeeAccountTypeId = '';
          }
        })
        .finally(() => {
          this.submitInit = true;
          this.submitLoading = false;
          this.budgetDialogVisible = false;
        });
    },
    submitWithdrawTransaction() {
      this.submitLoading = true;
      this.submitInit = false;
      let currentReasonType = this.reasonTypeList.filter((ele) => {
        return this.basicForm.reasonType === ele.value;
      })[0];
      initiateWithdrawTransaction({
        accountNo: this.basicForm.payerAccountNo == '' ? null : this.basicForm.payerAccountNo, // 主交易方账户
        bankAccountId: this.bankList.filter((item) => item.bankId == this.basicForm.bankAccountId)[0].assetId || '',
        amount: this.delcommafy(this.basicForm.amount),
        payerId: this.identityId,
        transactionTypeId: currentReasonType.transactionType.txnIndex,
        reasonTypeId: currentReasonType.reasonIndex,
        remark: this.basicForm.remark,
        serviceCode: this.basicForm.serviceCode,
      })
        .then((res) => {
          if (res.header && res.header.success) {
            this.$refs.basicFormRef.resetFields();
            this.basicForm.serviceCode = '';
            this.validVerifySuccess = false;
            this.showInitiateOrSucceed = false;
            this.tradeOrderId = res.tradeOrderId;
            this.payerAccountTypeId = '';
            this.payeeAccountTypeId = '';
          }
        })
        .finally(() => {
          this.submitInit = true;
          this.submitLoading = false;
          this.budgetDialogVisible = false;
        });
    },
    submit() {
      this.$refs.basicFormRef.validate((valid) => {
        if (valid) {
          if (this.showBankAccount) {
            this.submitWithdrawTransaction();
          } else {
            this.initTransactionPrepare();
          }
        }
      });
    },
    handleViewDetail() {
      let params = {
        tradeOrderId: this.tradeOrderId,
        identityId: this.identityId,
        identityType: '5000',
      };
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    handleContinueAddClick() {
      this.showInitiateOrSucceed = true;
    },

    getDiffIdentifierType() {
      if (!this.sameIdentity) {
        return this.basicForm.diffIdentifierType;
      } else {
        return '04';
      }
    },
    getReveiverIdentifier() {
      if (this.sameIdentity) {
        return this.basicForm.payeeShortCode;
      } else {
        switch (this.basicForm.diffIdentifierType) {
          case '04':
            return this.basicForm.payeeShortCode;
          case '17':
            return this.basicForm.payeeTillMsisdn;
          case '01':
            return this.basicForm.payeeMsisdn;
          case '03':
            return this.basicForm.payeeVoucherCode;
          default:
        }
      }
    },

    /**  输入框转金额的代码   */
    // 失去焦点金额格式化
    inputMoney(el) {
      let temp = Number(el.target.value) || null;
      this.basicForm.amount = this.priceFormat(temp);
    },
    // 获得焦点金额去掉格式
    unInputMoney(el) {
      if (el.target.value) {
        this.basicForm.amount = this.delcommafy(el.target.value);
      } else {
        this.basicForm.amount = null;
      }
    },
    priceFormat(num, n) {
      n = n || 2;
      let symbol = ',';
      if (num === null) return num;
      if (typeof num !== 'number') throw new TypeError('The num parameter should be of the number type.');
      if (n < 0) throw new Error('Parameter n should not be less than 0.');
      let hasDot = parseInt(num) !== num; // 这里检测num是否为小数，true表示小数
      let m = n !== undefined && n != null ? n : 1;
      num = m === 0 ? num.toFixed(m) + '.' : hasDot ? (n ? num.toFixed(n) : num) : num.toFixed(m);
      symbol = symbol || ',';
      num = num.toString().replace(/(\d)(?=(\d{3})+\.)/g, function (match, p1) {
        return p1 + symbol;
      });
      if (n === 0 || (!hasDot && !n)) {
        // 如果n为0或者传入的num是整数并且没有指定整数的保留位数，则去掉前面操作中的小数位
        num = num.substring(0, num.indexOf('.'));
      }
      return num;
    },
    // 去除千分位中的‘，’
    delcommafy(num) {
      if (!num) return num;
      num = num.toString();
      num = num.replace(/,/gi, '');
      if (num.indexOf('.00') > 0) num = parseInt(num);
      return num;
    },
    handleDialogOpened() {},
    handleDialogCancel() {
      this.budgetDialogVisible = false;
    },
    handleDialogConfirm() {
      this.initiateTransactionSubmit();
    },
    getBankList() {
      queryBankAccount({
        status: 'Active',
        identityId: this.identityId,
      }).then((res) => {
        if (res.header && res.header.success) {
          this.bankList = res.bankAccounts;
          this.basicForm.bankAccountId = '';
        } else {
          this.bankList = [];
        }
      });
    },
    getBankInfo(val) {
      this.bankInfoLoading = true;
      let currentBankAccount = this.bankList.filter((item) => item.bankId === val);
      queryBankInfo({
        bankId: val,
      })
        .then((res) => {
          if (res.header && res.header.success) {
            let financeService = [];
            const bankInfo = res ? res.bank : {};
            for (let key in bankInfo) {
              if (key === 'isDepositBank' && bankInfo[key] === true) {
                financeService.push('Deposit Bank');
              }
              if (key === 'isTrustBank' && bankInfo[key] === true) {
                financeService.push('Trust Bank');
              }
              if (key === 'isCardIssuingBank' && bankInfo[key] === true) {
                financeService.push('Card Issuing Bank');
              }
              this.bankInfos.forEach((item) => {
                if (item.value === key) {
                  item.content = bankInfo[key];
                }
                if (item.value === 'financeService') {
                  item.content = financeService.join(',');
                }
                if (item.value === 'trustBank') {
                  const isTrustBank = bankInfo.isTrustBank;
                  if (isTrustBank) {
                    item.content = bankInfo.bankName;
                  } else if (res.clearingBanks && res.clearingBanks.length > 0) {
                    item.content = res.clearingBanks[0].clearingBankName;
                  }
                }
                if (item.value === 'transferMode') {
                  item.content = this.transferModeOptions.find(
                    (item) => item.value === currentBankAccount[0].transferMode
                  ).label;
                }
              });
            }
          } else {
            this.bankInfos = [
              {
                label: 'Bank Short Code',
                value: 'bankShortCode',
                content: '',
              },
              {
                label: 'Bank Address',
                value: 'address',
                content: '',
              },
              {
                label: 'Finance Service',
                value: 'financeService',
                content: '',
              },
              {
                label: 'Trust Bank',
                value: 'trustBank',
                content: '',
              },
              {
                label: 'Transfer Mode',
                value: 'transferMode',
                content: '',
              },
            ];
          }
        })
        .finally(() => {
          this.bankInfoLoading = false;
        });
    },
  },
};
</script>

