import { selectRequiredValidateObj, inputRequiredValidateObj } from '@/common/validator/common';
import { verifyTransactionIdentity } from '@/views/identifier-center/api/api-transaction.js';

const checkEntityIsExist = (scope, entityType) => ({
  validator: (rule, value, callback) => {
    if (value) {
      const reqParams = {
        'short code': { shortCode: scope.basicForm.payeeShortCode },
        'voucher code': { voucherCode: scope.basicForm.payeeVoucherCode },
        msisdn: { msisdn: scope.basicForm.payeeMsisdn },
        'till msisdn': { tillMsisdn: scope.basicForm.payeeTillMsisdn },
      };

      verifyTransactionIdentity(reqParams[entityType])
        .then((res) => {
          if (res.header && res.header.success) {
            if (res.identityId) {
              callback();
            } else {
              if (entityType === 'msisdn') {
                return callback(new Error('The phone number does not meet the requirements.'));
              } else {
                return callback(new Error(`The ${entityType} is not exist.`));
              }
            }
          }
        })
        .catch(() => {
          return callback(new Error(`The ${entityType} Exception`));
        });
    }
  },
  trigger: 'blur',
});

let selectRequiredForSubmitValidateObj = {
  required: true,
  message: 'Please select content',
  trigger: 'submit',
};

const getTransactionBasicFormRules = (scope) => {
  return {
    bankAccount: [selectRequiredValidateObj],
    serviceCode: [selectRequiredForSubmitValidateObj],
    code: [selectRequiredValidateObj],
    payerShortCode: [inputRequiredValidateObj],
    payeeShortCode: [inputRequiredValidateObj, checkEntityIsExist(scope, 'short code')],
    amount: [inputRequiredValidateObj],
    reasonMode: [selectRequiredValidateObj],
    reason: [inputRequiredValidateObj],
    reasonType: [selectRequiredValidateObj],
    currency: [inputRequiredValidateObj],
    diffIdentifierType: [selectRequiredValidateObj],
    payeeVoucherCode: [inputRequiredValidateObj, checkEntityIsExist(scope, 'voucher code')],
    payeeMsisdn: [inputRequiredValidateObj, checkEntityIsExist(scope, 'msisdn')],
    payeeTillMsisdn: [inputRequiredValidateObj, checkEntityIsExist(scope, 'till msisdn')],
  };
};

export { getTransactionBasicFormRules };
